@use 'sass:map';

@import './variables';

@mixin text($fontFamily, $fontSize) {
  font-family: $fontFamily; //'Arial', 'sans-serif'
  font-size: $fontSize;  //16px;
  line-height: 1.2;
  color: $textColor;
}

// mixin for adaptability 
@mixin max-bp($breakpoint) {
  @media (max-width: map.get($breakpoints, $breakpoint)) { 
    @content
  }
}

// adaptability settings width

@mixin adaptabilityWidth($miniWidth:95%) {
  width: 80%;
  @include max-bp('small') {
    width:90%;
    
  }
  @include max-bp('mini') {
    width: $miniWidth;
  }
}

//page block template 

@mixin templatePageBlock($backColor, $minHeight:100vh) {
  position: relative;
  min-height: $minHeight;
  display: flex;
  // align-items: center;
  background-color: $generalBackgroundColor;

  
}

// page container template

@mixin templatePageContainer($flexDirection:column) {
  width: 80%;
  margin: 0 auto;
  
  display: flex;
  flex-direction: $flexDirection;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  // border-left: 1px solid;
  // border-right: 1px solid;
  padding-top: 50px;
  

  @include max-bp('strech') {
    width: 90%;
  }

  @include max-bp('mini') {
    width: 95%;
  }

}

//  template 'box for items, item-block'

@mixin itemsBox {
  width: 100%;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-top: 10vh;
}

@mixin itemBlock($width, $minHeight: 300px) {
  min-height: $minHeight;
    width: $width;
    margin: 10px ;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include max-bp('mini') {
      width: 250px;
      margin: 10px 0;
  }
}

@mixin sideNamePage{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;  
  display: flex;    
  flex-flow: row nowrap;   
  justify-content: center;
  align-items: center;
  width: calc((100vw - 27.875em) / 2);
  min-width: 1.36em;
  max-height: 100%;
  margin: 0;
  font-size: 2.5em;
  font-weight: 900;
  line-height: 1;
  color: #e9e9e9;
  text-transform: uppercase;    
  writing-mode: tb-rl;
  text-align: center;
  transform: rotate(180deg);
}
